.App {
  text-align: center;
  font-family: Arial, sans-serif;
  background-color: #2c2c2c;
  color: #ffffff;
  padding: 20px;
}

.laskuri {
  background-color: #3c3c3c;
  border: 1px solid #555;
  border-radius: 8px;
  padding: 20px;
  margin: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: inline-block;
  width: 200px;
}

.laskuri h2 {
  margin-bottom: 10px;
  font-size: 1.2em;
  color: #ffffff;
}

.laskuri button {
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  padding: 10px;
  margin: 5px;
  cursor: pointer;
  font-size: 1em;
}

.laskuri button:hover {
  background-color: #0056b3;
}

.laskuri span {
  display: inline-block;
  width: 40px;
  text-align: center;
  font-size: 1.2em;
  margin: 0 10px;
  color: #ffffff;
}

.laskuri p {
  margin-top: 10px;
  font-size: 0.9em;
  color: #cccccc;
}

.yhteistulos {
  margin-top: 20px;
  font-size: 1.5em;
  color: #ffffff;
}

.input-container {
  margin-top: 20px;
}

.input-container input {
  padding: 10px;
  font-size: 1em;
  border: 1px solid #555;
  border-radius: 4px;
  margin-right: 10px;
  background-color: #3c3c3c;
  color: #ffffff;
}

.input-container button {
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  padding: 10px;
  cursor: pointer;
  font-size: 1em;
}

.input-container button:hover {
  background-color: #0056b3;
}

.tulokset {
  margin-top: 20px;
  text-align: left;
}

.tulokset ul {
  list-style-type: none;
  padding: 0;
}

.tulokset li {
  background-color: #3c3c3c;
  border: 1px solid #555;
  border-radius: 4px;
  padding: 10px;
  margin: 5px 0;
  color: #ffffff;
}

button {
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  padding: 10px;
  margin: 20px 0;
  cursor: pointer;
  font-size: 1em;
}

button:hover {
  background-color: #0056b3;
}
body{
  background-color: #2c2c2c;;
}